@keyframes toBigKeyFrames {
  0% {
    transform: scale(0);
  }
  70% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.animaToBig {
  animation-name: toBigKeyFrames;     
  animation-timing-function: linear;    
  animation-delay: 0s;   
  animation-duration: 0.2s; 
}
